.offer {
	display: flex;
	width: 100%;
	max-width: 1200px;
	height: fit-content;
	justify-content: center;
	align-items: baseline;
	margin-top: 80px;
}

@media (max-width: 1000px) {
	.offer {
		flex-direction: column;
		align-items: center;
		gap: 40px;
		padding-bottom: 50px;
	}
}
