@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
body {
	margin: 0;
	padding: 0;
	font-family: "Montserrat", sans-serif;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
* {
	box-sizing: border-box;
}

.app {
	height: 100vh;
	width: 100%;
	background-color: #fff;
	background: url(/pricing_example/static/media/bg-top.4980c1fd.svg) right 0px top no-repeat,
		url(/pricing_example/static/media/bg-bottom.0ed1debd.svg) left 0 bottom no-repeat;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

@media (max-width: 600px) {
	.app {
		background: url(/pricing_example/static/media/bg-top.4980c1fd.svg) right -200px top no-repeat;
	}
}

.heading {
	/* color: hsl(233, 13%, 49%); */
	color: hsl(234, 14%, 74%);
	margin-top: 45px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}
.switch_Container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 30px;
	gap: 30px;
}
.switch_Container span {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

.switch {
	height: 40px;
	width: 75px;
	border-radius: 25px;
	background: rgb(163, 168, 240);
	background: linear-gradient(
		90deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	position: relative;
	cursor: pointer;
}
.switch:hover {
	background: hsl(236, 72%, 79%);
}
.dot {
	background-color: #fff;
	width: 30px;
	height: 30px;
	border-radius: 25px;
	margin: 0 5px;
	position: absolute;
	transition: 0.3s ease-in-out transform;
}

.priceTab,
.priceTab_Purple {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 400px;
	-webkit-align-items: center;
	        align-items: center;
	box-shadow: 0 0 10px hsla(234, 14%, 74%, 0.75);
	border-radius: 10px;
	padding: 20px 10px;
	color: hsl(232, 13%, 33%);
	background-color: #fff;
}
.priceTab_Purple {
	padding: 50px 0;
	color: white;
	background: rgb(163, 168, 240);
	background: linear-gradient(
		180deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
}
.type,
.type_Purple {
	font-size: 21px;
	padding: 15px;
	color: #75788b;
}
.type_Purple {
	color: white;
}
.price {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 70px;
	grid-gap: 5px;
	gap: 5px;
	margin-bottom: 20px;
}
.dollar {
	font-size: 45px;
}
.option,
.option_Purple {
	color: #75788b;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 20px;
	width: 90%;
	border-top: 1px solid hsla(234, 14%, 74%, 0.5);
}
.option_Purple {
	color: white;
}

.last_option {
	border-bottom: 1px solid hsla(234, 14%, 74%, 0.5);
}
.learn_Button,
.learn_Button_Purple {
	background: rgb(163, 168, 240);
	background: linear-gradient(
		90deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
	width: 90%;
	height: 50px;
	border-radius: 8px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	text-transform: uppercase;
	color: white;
	font-size: 15px;
	margin-top: 40px;
	margin-bottom: 20px;
	cursor: pointer;
}
.learn_Button:hover {
	border: 1px solid #75788b;
	background: white;
	color: rgba(105, 111, 221, 1);
}
.learn_Button_Purple:hover {
	background: transparent;
	border: 1px solid white;
	color: white;
}
.learn_Button_Purple {
	background: white;
	color: rgba(105, 111, 221, 1);
}

@media (max-width: 500px) {
	.priceTab,
	.priceTab_Purple {
		width: 90%;
	}
}

.offer {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	max-width: 1200px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: baseline;
	        align-items: baseline;
	margin-top: 80px;
}

@media (max-width: 1000px) {
	.offer {
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-align-items: center;
		        align-items: center;
		grid-gap: 40px;
		gap: 40px;
		padding-bottom: 50px;
	}
}

